<template>
  <div class="bc-catalog-modal-layer">
    <div class="catalog-modal-layer-conetne">
      <div class="catalog-modal-layer-conetne-header">
        <a href="javascript:;" @click="closeModal">
          <i class="el-icon-close"></i>
        </a>
      </div>
      <div class="catalog-modal-layer-conetne-div">
        文章大纲：
        <el-input v-model="typeInput" placeholder="请输入文章大纲"></el-input>
      </div>
      <div class="catalog-modal-layer-conetne-div">
        文章描述：
        <el-input v-model="describeInput" placeholder="请输入大纲描述"></el-input>
      </div>
      <div class="catalog-modal-layer-conetne-btn">
        <el-button type="primary" @click="modifyOutline">确认修改</el-button>
      </div>
    </div>
  </div>
</template>


<script>
import * as ajax from "@/api";

export default {
  data() {
    return {
      typeInput: "",
      describeInput: "",
      data: ""
    };
  },
  props: ["modalData"],
  created() {},
  watch: {
    modalData(val) {
      this.describeInput = val["book_describe"];
      this.typeInput = val["book_name"];
      this.data = val;
    }
  },
  methods: {
    toggleModle(data) {
      this.$emit("toggleModle",data); //修改成功关闭模态框
    },
    closeModal(){
      this.$emit("close"); //关闭模态框
    },
    modifyOutline() {
      ajax.modifySecondTitle(
          this.data.id,
          this.typeInput,
          this.describeInput,
          this.data.type_id,
        )
        .then(resp => {
          if (resp.data.code) {
            alert("修改成功");
            this.toggleModle(resp.data.data.article_category);
          }
        });
    }
  }
};
</script>


<style lang='scss'>
.bc-catalog-modal-layer {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  background-color: rgba(27, 25, 25, 0.5);
  z-index: 99;
  .catalog-modal-layer-conetne {
    width: 400px;
    height: 500px;
    background-color: #fff;
    position: absolute;
    margin: auto;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 9999999;
    &-header {
      height: 50px;
      border-bottom: 1px solid #999;
      a {
        float: right;
        font-size: 30px;
        margin-right: 10px;
        margin-top: 5px;
        color: #666;
      }
    }
    .catalog-modal-layer-conetne-div {
      margin-top: 20px;
    }
    .catalog-modal-layer-conetne-btn {
      margin-top: 100px;
      text-align: center;
    }
  }
}
</style>